<template>
    <div class="search">
        <universal-filters v-model="filters" class="search__filters" :save-to-url="saveFiltersToUrl" />

        <div class="search__instruction">
          <slot name="instruction" />
        </div>

        <p v-if="!apps.length && !isSearching">{{ $t('NOTHING_WAS_FOUND') }}</p>

        <div class="search__result" v-infinite-scroll="searchNextPage">
            <universal-app
                v-for="app in apps"
                :app="app"
                :custom-action="appCustomAction"
                :custom-action-label="appCustomActionLabel"
                @custom-action-triggered="$emit('app-custom-action-triggered', $event)"
                :shareable="appShareable"
                @update-filters="updateFilters"
                :key="app.universal_object_identifier"
            />
        </div>

        <v-loader
          v-if="!isLastPage"
          class="search__loading-spinner"
          :class="{
            'search__loading-spinner_visible': isSearching
          }"
        />
    </div>
</template>

<script>
import infiniteScroll from 'vue-infinite-scroll';
import VLoader from '@/components/Loader';
import { isEqual } from 'lodash';
import UniversalFilters from './UniversalFilters.vue';
import UniversalApp from './UniversalApp.vue';

export default {
  directives: {
    infiniteScroll,
  },
  components: { VLoader, UniversalFilters, UniversalApp },
  props: {
    appCustomAction: Boolean,
    appCustomActionLabel: String,
    appShareable: Boolean,
    saveFiltersToUrl: Boolean,
  },
  data() {
    return {
      pagination: {
        page: 1,
        pageSize: 25,
      },
      filters: {},
      apps: [],
      searchController: null,
      isSearching: true,
      isSearchAborted: false,
      isLastPage: false,
    };
  },
  computed: {
    title() {
      return this.$t('SEARCH');
    },
  },
  methods: {
    async search() {
      if (this.isSearching && this.searchController) {
        this.searchController.abort();
        this.isSearchAborted = true;
      }

      this.isSearching = true;

      this.searchController = new AbortController();
      try {
        const response = await this.$api.searchIndex({
          ...this.filters,
          start: (this.pagination.page - 1) * this.pagination.pageSize,
          length: this.pagination.pageSize,
        }, { signal: this.searchController.signal });

        if (!response.length) {
          this.isLastPage = true;
        }

        this.apps.push(...response);
      } finally {
        if (!this.isSearchAborted) {
          this.isSearching = false;
        }

        this.isSearchAborted = false;
      }
    },
    searchNextPage() {
      if (this.isSearching || this.isLastPage) {
        return;
      }

      this.pagination.page = this.pagination.page + 1;
      this.search();
    },
    updateFilters(filters) {
      this.$set(this, 'filters', {
        ...this.filters,
        ...filters,
      });
    },
  },
  watch: {
    filters: {
      handler(newValue, oldValue) {
        if (isEqual(newValue, oldValue)) {
          return;
        }

        this.pagination.page = 1;
        this.apps = [];
        this.isLastPage = false;
        this.search();
      },
      deep: true,
    },
  },
  created() {
    this.search();
  },
};
</script>

<style lang="scss" scoped>
.search {
    &__result {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
        margin-top: 15px;
    }

    &__instruction,
    &__loading-spinner {
      margin-top: 15px;
    }

    &__loading-spinner {
      opacity: 0;

      &_visible {
        opacity: 1;
      }
    }
}
</style>
