    height: 57px;
    border-radius: 22.5%;
    mask: url(../../assets/ios-mask.svg) center/100% 100% no-repeat;
    object-fit: cover;
    object-position: center;

    &_stub {
        background-color: var(--stub-bg);
    }
}

.header,
.info,
.details {
    min-width: 0;
}

